.heroicInspirationOuterbox {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.heroicInspirationLabel {
    font-size: 1em;
    font-weight: bold;
    text-transform: uppercase;
    /* I used to scale this fucking fatass font, but then I got smart and used the shortened versions: transform: scale(0.5,1); */
    padding: 0.5em;
    text-align: center;
    line-height: 1.25em;
}

.heroicInspirationContainer {
    display: grid;
    grid-template-columns: auto;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    padding: 0.5em;
    border-top: 2px solid black;

    flex: 1;
}

.heroicInspirationContainer > * {
    height: 5em !important;
    width: 5em !important;
}

.heroicInspirationContainer > input[type="checkbox"]::before {
    font-size: 3em;
    font-weight: normal;
}